#root {
  .album-page {
    height: 100%;
    .album-wrapper {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      .left-bookcover {
        background-color: #e31d2e;
        border-radius: 0 10px 10px 0;
        width: 650px;
        height: 700px;
        display: flex;
        justify-content: end;
        align-items: center;
        box-shadow: -5px 0px 20px 10px #b51724 inset;
        .left-album {
          background-color: #fce8ea;
          width: 630px;
          height: 650px;
          border-radius: 0 10px 10px 0;
          display: flex;
          align-items: center;
          justify-content: center;
          box-shadow: -5px 0px 20px 5px #e2d0d2 inset;
          .left-border {
            border: 1px solid #e31d2e;
            width: 550px;
            height: 600px;
            display: flex;
            flex-wrap: wrap;
            gap: 20px;
            padding: 20px 20px;
            box-sizing: border-box;
          }
        }
      }
      .right-bookcover {
        background-color: #e31d2e;
        border-radius: 10px 0 0 10px;
        border-left: 1px solid black;
        width: 650px;
        height: 700px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        box-shadow: 5px 0px 20px 10px #b51724 inset;
        .right-album {
          border-radius: 10px 0 0 10px;
          background-color: #fce8ea;
          width: 630px;
          height: 650px;
          display: flex;
          align-items: center;
          justify-content: center;
          box-shadow: 5px 0px 20px 5px #e2d0d2 inset;
          .right-border {
            width: 550px;
            height: 600px;
            border: 1px solid #e31d2e;
          }
        }
      }
    }
  }
}
